@import url(https://fonts.googleapis.com/css2?family=Jost:wght@500;700&display=swap);
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}.has-padding-top{padding-top:20px}.has-padding-bottom{padding-bottom:20px}.has-margin-top{margin-top:20px}.has-margin-bottom{margin-bottom:20px}.nudge-top{margin-top:10px}.nudge-bottom{margin-bottom:10px}.hidden{display:none !important}html,body{-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:100%;background-color:#9146FF;overflow-x:hidden}body{color:#f0f0f0;font-family:'Jost', sans-serif;font-size:18px;font-weight:500;line-height:1.5;margin:0;min-height:100vh}.container{margin:0 auto;max-width:750px;width:calc(100% - 2rem);padding:0.5rem 1rem}main.container{margin-top:1.5rem}button{padding:1rem 2rem;cursor:pointer;background-color:#f0f0f0;border:0;color:white;font-weight:700;font-size:1rem}.warning{display:flex;align-items:center;margin-bottom:1.5rem;padding:0 0.5rem 0 0;background:crimson;color:white;font-weight:700;border-radius:5px}.warning:before{content:'!';background-color:rgba(255,255,255,0.3);padding:0.5rem 1rem;margin-right:1rem;border-top-left-radius:5px;border-bottom-left-radius:5px}header{background:transparent;padding:1rem;line-height:1;color:#f0f0f0}header .container{display:flex;-moz-justify-content:space-between;-ms-justify-content:space-between;justify-content:space-between;-ms-flex-pack:space-between;-moz-align-items:center;-ms-align-items:center;align-items:center}.logo-container{display:flex;font-size:16px}.logo{max-height:32px;margin-right:0.5rem}.link-container a{line-height:1}.link-container img{height:48px;width:175px}.uploader{display:flex;-moz-justify-content:center;-ms-justify-content:center;justify-content:center;-ms-flex-pack:center;-moz-align-items:center;-ms-align-items:center;align-items:center;-moz-flex-direction:column;flex-direction:column;height:350px;background:white;color:#9146FF;border-radius:5px}.uploader input{position:absolute;top:-50rem;opacity:0}.uploader label{cursor:pointer}.upload-icon{height:48px;margin-bottom:1rem}

